@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.CardView {
    display:flex;
    flex-wrap: wrap;
}

.ShopCard {
    display: flex;
    flex-direction: column; 
    border: 1px solid;
    margin: 5px;
    width: 32.3%;
}

.crad-container {
    display: flex;
    flex-direction: column;
}

.material-icons {
    align-self: flex-end;
    font-size: 36px;
    margin-right: 2px;
}

.card-name, .card-color {
    text-align: center;
}


.card-footer {
    display: flex;
    justify-content: space-around;
    padding: 10px;
}

button {
    border: 2px solid red;
    border-radius: 10px;
    color: red;
}

.ShopItem {
    display: flex;
    list-style-type: none;
    border: 1px solid;
    justify-content: space-between;
    align-items: center;
}

.item-img-Items {
    height: 50px;
    width: 55px;
}

.price-Item {
    margin-right: 100px;
}

.ListView {
    width: 99%;
    margin-left: 10px;
}

.list-button {
    margin-right: 5px;
}


